.box {
    position: relative;
    width: 100vw;
    height: 100vh;
    padding-top: 60px;
}

.hero {
    width: 100vw;
    height: 100vh;
    overflow: hidden;
    direction: rtl;
    pointer-events: none;
}

.content {
    position: relative;
    background-color: rgb(252, 252, 252);
    z-index: 1000000;
}

.text {
    position: absolute;
    display: flex;
    justify-content: center;
    width: 100vw;
    height: 100vh;
}

.flagship {
    position: fixed;
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    /* transform: translateX(60px); */
}

.centerstack {
    pointer-events: none;
    width: max(100vw, 1300px);
    height: 100vh;
    position: relative;
}

.heroimg {
    user-select: none;
    position: absolute;
    bottom: 0;
    right: 0;
}

.hero-bird {
    z-index: 100;
    top: 0;
    transform: scale(0.1);
    left: 0; 
    right: 0; 
    margin-left: auto; 
    margin-right: auto; 
}


.text {
    mix-blend-mode: multiply;
}

.center-callout {
    font-weight: 500;
    display: flex;
    flex-direction: column;
    color: var(--darkergreen);
}

.center-buttons {
    display: flex;
    gap: 30px;
    display: inline-block;
    float: right;
}

.center-button {
    display: inline-block;
    color: var(--darkergreen);
    font-weight: 600;
    border-radius: 8px;
    padding: 10px;
    cursor: pointer;
    margin: 0 5px 0 0;
}

.center-button.big {
    display: inline;
    background: var(--darkergreen);
    color: var(--white);
}

@media only screen and (max-width: 600px) {
    .text {
        z-index: 100 !important;
    }
    .flagship {
        padding: 10vw;
    }
    .center-callout {
        font-size: max(50px, 10vw);
        transform: translateY(-10px);
        gap: 10px;
        line-height: 60px;
    }
    .center-buttons {
        font-size: 20px;
        transform: translate(-18px, 10px);
    }
}

@media only screen and (min-width: 600px) {
    .text {
        z-index: 100 !important;
    }
    .flagship {
        padding: 10vw;
    }
    .center-callout {
        font-size: max(50px, 10vw);
        transform: translateY(-10px);
        gap: 10px;
        line-height: 83px;
    }
    .center-buttons {
        font-size: 20px;
        transform: translate(-15px, 10px);
    }
}
@media only screen and (min-width: 924px) {
    .text {
        z-index: 0 !important;
    }
    .flagship {
        padding: 40px;
        transform: translateY(-35px);
    }
    .center-callout {
        font-size: 80px;
        padding-top: 40px;
        gap: 20px;
        line-height: 85px;

    }
    .center-buttons {
        font-size: 23px;
        transform: translate(-200px, 30px);
    }
}


.callout-bold {
    font-weight: 700;
    display: block;
}

.center-subtitle {
    position: absolute;
    z-index: 100000 !important;
    bottom: 10px;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}


.install-codeblock {
    font-family: IBM Plex Mono, monospace;
    background: rgba(0,0,0, 0.7);
    box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
    backdrop-filter: blur(5px);
    -webkit-backdrop-filter: blur(5px);
    display: inline-block;
    font-size: 20px;
    font-weight: 800;
    margin-bottom: 20px;
    color: var(--offwhite);
    padding: 5px 15px;
    border-radius: 10px;
}

.header-container {
    width: 100%;
    position: fixed;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 20px;
    z-index: 10000000000000000000;
}

.heading {
    font-family: Open Sans, sans-serif;
    font-weight: 700;
    font-size: 30px;
}

.heading a {
    color: var(--black) !important;
}

.heading-right {
    color: var(--darkergreen);
    font-weight: 600;
    font-family: "IBM Plex Sans", -apple-system, BlinkMacSystemFont, sans-serif;
    display: flex;
    gap: 20px;
}


.explain-heading {
    color: var(--darkergreen);
    font-weight: 800;
    font-size: 35px;
    padding: 30px 0 0 0;
}

.explain-text {
    padding: 5px 0;
    font-size: 18px;
}

.hljs-string {
    white-space: nowrap;
}

.hidebar {
    -ms-overflow-style: none;  /* Internet Explorer 10+ */
    scrollbar-width: none;  /* Firefox */
}
.hidebar::-webkit-scrollbar { 
    display: none;  /* Safari and Chrome */
}

.qsg {
    padding: 5px;
    margin: 10px 3px;
    background: var(--darkergreen);
    color: var(--white);
    font-weight: 800;
    border-radius: 4px;
}

.badges {
    margin: 30px 0;
}

.badge {
    height: 60px;
}

.footer {
    position: sticky;
    bottom: 0;
    text-align: center;
}
